<template>
    <v-container fluid fill-height style="padding:0;" id="drag" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Drag">拖拽下面的圆到另外一个div中</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text style="position: relative;">
                                    <div style="height: 200px;" class="clearfix">
                                        <div class="box">
                                            <div class="circle" draggable="true">
                                                <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
                                                    <circle draggable="true" cx="100" cy="100" r="50" fill="blueviolet"/>
                                                </svg>
                                            </div>
                                        </div>
                                        <div class="box"></div>
                                    </div>

                                    <v-flex xs12>
                                        <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                            <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                            <v-tab key="html" ripple>HTML</v-tab>
                                            <v-tab key="css" ripple>CSS</v-tab>

                                            <v-tab-item key="js">
                                                <v-card flat>
                                                    <v-card-text>
                                                        <pre class="code">{{pres[0]}}</pre>
                                                    </v-card-text>
                                                </v-card>
                                            </v-tab-item>

                                            <v-tab-item key="html">
                                                <v-card flat>
                                                    <v-card-text>
                                                        <pre class="code">{{pres[1]}}</pre>
                                                    </v-card-text>
                                                </v-card>
                                            </v-tab-item>

                                            <v-tab-item key="css">
                                                <v-card flat>
                                                    <v-card-text>
                                                        <pre class="code">{{pres[2]}}</pre>
                                                    </v-card-text>
                                                </v-card>
                                            </v-tab-item>
                                        </v-tabs>
                                    </v-flex>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-5-21",
      title: "拖拽dom元素",
      items: [
        {
          title: "拖拽下面的圆到另外一个div中",
          target: "#Drag"
        },
        { divider: true },
      ],
      pres: 
[
`
    const circle = document.querySelector('.circle');
        
    // 当拖拽的元素进入目标元素时触发
    document.addEventListener("dragenter", function(e) {
        if (e.target.className == "box") {
            e.target.style.opacity = .5;
        }
    }, false);

    // 当拖拽的元素进入离开元素时触发
    document.addEventListener("dragleave", function(e) {
        if (e.target.className == "box") {
            e.target.style.opacity = 1;
        }
    }, false);

    document.addEventListener("dragover", function(e) {
        // 拖动的元素悬停在目标元素上方执行
        // 阻止默认行为，可以允许drop
        e.preventDefault();
    }, false);

    // 当元素在目标元素松手了触发
    document.addEventListener("drop", function(e) {
        e.preventDefault();
        if (event.target.className == "box") {
            e.target.style.opacity = 1;
            circle.parentNode.removeChild( circle );
            e.target.appendChild( circle );
        }
    }, false);

    // 其他的拖拽事件
    circle.addEventListener("drag", function(e) {}, false);         // 拖动元素触发
    circle.addEventListener("dragstart", function(e) {}, false);    // 开始拖动执行
    circle.addEventListener("dragend", function(e) {}, false);      // 结束拖动执行
`,
`
    <div style="height: 200px;" class="clearfix">
        <div class="box">
            <div class="circle" draggable="true">
                <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
                    <circle draggable="true" cx="100" cy="100" r="50" fill="blueviolet"/>
                </svg>
            </div>
        </div>
        <div class="box"></div>
    </div>
`,
`
    .box{
        background-color: antiquewhite;
        width: 200px;
        height: 200px;
        float: left;
        margin-right: 15px;
    }
    .box .circle{
        clip-path: circle(50px at 50% 50%);
        shape-outside: circle(50%);
        background-color: transparent;
        width: 200px;
        height: 200px;
    }    

    .clearfix::after {
        content: '';
        display: block;
        clear: both;
    }  
`
]
    };
  },
    mounted() {
        const circle = document.querySelector('.circle');
        
        document.addEventListener("dragenter", function(e) {
            if (e.target.className == "box") {
                e.target.style.opacity = .5;
            }
        }, false);

        document.addEventListener("dragleave", function(e) {
            if (e.target.className == "box") {
                e.target.style.opacity = 1;
            }
        }, false);

        document.addEventListener("drop", function(e) {
            e.preventDefault();
            if (event.target.className == "box") {
                e.target.style.opacity = 1;
                circle.parentNode.removeChild( circle );
                e.target.appendChild( circle );
            }
        }, false);
        document.addEventListener("dragover", function(e) {
            e.preventDefault();
        }, false);
    },
    methods: {
    },
    destroyed() {

    },
};
</script>

<style lang="scss">
#drag {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
        margin-bottom: 15px;
    }     

    .box{
        background-color: antiquewhite;
        width: 200px;
        height: 200px;
        float: left;
        margin-right: 15px;
    }
    .box .circle{
        clip-path: circle(50px at 50% 50%);
        shape-outside: circle(50%);
        background-color: transparent;
        width: 200px;
        height: 200px;
    }    

    .clearfix::after {
        content: '';
        display: block;
        clear: both;
    }                        
}
</style>

